body {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: row;
	font-family: "Roboto";
}

.trafic {
	margin-top: 180px;
}

.lighter {
	display: flex;
	height: 100px;
	width: 100px;
	opacity: 50%;
	border-radius: 50%;
	margin-top: 10px;
	text-align: center;
	align-items: center;
	justify-content: center;
	font-size: 60px;
	font-weight: 100;
	color: aliceblue;
}

.red {
	background-color: red;
}

.yellow {
	background-color: yellow;
}

.green {
	background-color: green;
}

.active {
	opacity: 100%;
}
